*{
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}
body {
  font-family:"Aktiv Grotesk", -apple-system, BlinkMacSystemFont, "San Francisco", "Segoe UI", Roboto, "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.app{
  min-height: 100vh;
  max-height: 100%;
  color: white;
  padding: 10px 0; 
  background-color: #222;
}
.container{
  width: 90%;
  margin: 0 auto;
}
.cardsList{
  width: 100%;
}
button{
  border: none;
  margin-right: 10px;
  cursor: pointer;
  font-size: 18px;
}
.card{
border-radius: 10px;
overflow: hidden;
  background: #f4f4f4;
  margin: 50px auto;
  color: #333;
  
  max-width: 350px;
}
.card .text{
  padding: 20px;
  font-size: 14px;
}
.name{
  margin: 10px 0;
  font-size: 22px;
}

.splitter{
  display: flex;
  justify-content: space-between;
  align-items: center;

}
.card .text .price{
  background: black;
  color: #cccccc;
  padding:  8px;
  border-radius: 5px;
  font-size: 14px;
}

.opener{
 padding: 20px;
}
.otherRentersList{
  opacity: 0;
  transition: .5s;
  height: 0;
  overflow: scroll;
  transition: all .5s;
}
.otherRentersList::-webkit-scrollbar{
  width: 2px;
  height: 0px;
  background-color: #f4f4f4;
}
.otherRentersList::-webkit-scrollbar-thumb{
  background-color: rgb(153, 153, 153);
}
.active{
  opacity: 1;
 height: 250px;
  transition: all .5s;
}
.filteredCard{
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid rgb(212, 212, 212);
  margin: 20px 0;
  padding: 10px;
  min-width: 100%;
  border-radius: 10px;
  
}
.filteredCard:hover{
  background-color: #ebebeb;
}
.filteredCard .img{
margin-right: 10px;
min-height: 100%;

}
.filteredCard .img img{
  object-fit: fill;
  border-radius: 5px;
}
.filteredCard .filteredTxt{
  margin-right: 0;
}
.filteredCard .price{
  color:rgba(255, 0, 0, 0.712);
}
.rate{
  border: 1px solid rgb(214, 214, 214);
 text-align: center;
 padding: 5px 10px;
  border-radius: 15px;
  background-color: rgb(207, 207, 207);
  font-size: 10px;
  min-width:50px ;
}
.imgNTxt{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
@media screen and (max-width:350px){
  .filteredCard{
  font-size: 80%;
  }
.rate{
  display: none;
}
}

@media screen and (min-width:850px){
  .card{
    max-width: 800px;
    margin: 50px 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  .card .opener{
    
    width: 100%;
  }
  .imgWTxt{
    border-right: 1px solid rgba(209, 209, 209, 0.788);
  }
  .openerBtn{
    display: none;
  }
  .otherRentersList{
    opacity: 1;
    transition: .5s;
   overflow: scroll;
    transition: all .5s;
   height: 350px;
   padding-right: 10px;
  }
  .otherRentersList::-webkit-scrollbar{
    width: 3px;
    height: 0px;
    background-color: #f4f4f4;
    
  }
  .otherRentersList::-webkit-scrollbar-thumb{
    background-color: rgb(153, 153, 153);
    border-right: 10px;
  }
  
}
